<script>
export default {
  name: 'app',
  data(){
	  return{
		  colors:[
			  'bf975a','E3B79E', 'F1D0AF', 'EAD4AB', 'F0C9A2', 'F1C9AF', 'CCA07D' , 'D1A179', 'D19B6F', 'BC9584', '704C2C', '73461D', '734224', '70391B', '5C331F', '4a352c'
			],
			restartAtMax: ''
	  }
	},
	methods:{
		colorTransition(){
			for (let i = 0; i < this.colors.length; i++) {
				this.changeFill(i)
			}
		},
		changeFill(i){
			const element = this.colors[i];
			setTimeout(() => {
				this.$el.querySelector(".politie-logo").style.fill = `#${element}`
				this.$el.querySelector(".logo-schaduw").style.fill = `#${element}`
				this.checkMax()
				}, 1000 +(i*1000));				
		},
		checkMax(){
			this.restartAtMax++
			if(this.restartAtMax == this.colors.length){
				this.colorTransition()
				this.restartAtMax = ''
			}
		}
	},
	created(){
		setTimeout(()=>{
			this.colorTransition()
		},3000)
	}
  
}
</script>

<style>
	svg{
		max-width: 250px;
	}
	.logo-schaduw, .politie-logo{
		transition: 1s;
	}
	.logo-schaduw{
		fill:#bf975a;
		opacity: .3;
	}
	.politie-logo{
		fill:#bf975a;
	}
</style>